import {
  headerColor,
  upperBodyColor,
  lowerBodyColor,
  skillsColor,
} from "../../material-kit-react";

const cardStyle = {
  card: {
    border: "0",
    marginBottom: "0",
    marginTop: "0",
    borderRadius: "0",
    color: "rgba(0, 0, 0, 0.87)",
    background: "#fff",
    width: "100%",
    boxShadow:
      "0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12)",
    position: "relative",
    display: "flex",
    flexDirection: "column",
    minWidth: "0",
    wordWrap: "break-word",
    fontSize: ".875rem",
    transition: "all 300ms linear",
    alignItems: "center",
  },
  cardPlain: {
    background: "transparent",
    boxShadow: "none"
  },
  cardCarousel: {
    overflow: "hidden",
    display: "inline-block",
    height: "15rem",
    width: "15rem",
  },
  upperBody: {
    backgroundColor: upperBodyColor,
    color: "#FFFFFF"
  },
  lowerBody: {
    backgroundColor: lowerBodyColor,
    color: headerColor
  },
  skills:{
    backgroundColor: skillsColor,
    color: headerColor
  }
};

export default cardStyle;
